div > button {
    background-color: rgba(0,0,0,1);
    border: 0px;
    font-size: 14px;
    margin: 50px 50px;
    color: #f3f3f3;
    border-radius: 5px;
    padding: 10px;
    font-weight: lighter;
    transition: all 0.2s ease-in-out;
    
}

.button-container{
    width: 600px;
    display: flex;
    justify-content: center;
    
}

button:hover{
    background-color: rgba(255,255,255,1);
    color: black;
    
}

button:active{
    background-color: rgba(255,255,255,1);
    color: black;
    box-shadow: 0 5px 15px rgba(255,255,255,0.4);
}

