div > select {
  text-transform: capitalize;
}

.blackbox-disp {
  display: flex;
  flex-direction: column;
  background-color: #030303;
  width: 600px;
  height: 200px;
  border-radius: 4px;
  box-shadow: -12px 36px 54px 18px rgba(34, 39, 53, 0.43);
}
.header {
  color: rgb(206, 204, 204);
  text-align: right;
  padding-top: 20px;
  padding-right: 60px;
  font-size: 12px;
  text-transform: uppercase;
}

.counter-disp {
  width: 80%;
  margin: auto auto;
  display: flex;
  flex-direction: row;
  margin-top: 10px;
}
